.main{
    &_warning{
        transform: none;
        z-index: 1;
    }

    &_heading{
        cursor: pointer;
        // text-decoration-line: underline;
        margin: 1rem auto;
        font-size: 2rem;
        width: 70%;
        margin-bottom: 1.25rem;
        font-weight: 500;
        // font-family: "Raleway", sans-serif;
        
        &::after{
            display: none;
        }
    }

    &_window{
        width: 70% !important;
        max-width: 70% !important;
        max-height: 90vh;
        height: 90vh !important;
    }

    &_chat{
        top: 0;
        right: 0;
        position: absolute;
        // position: fixed;
    }
}

.wide{
    width: calc(100% - 2rem) !important;
    max-width: calc(100% - 2rem) !important;
    margin: auto !important;
}